@import url("https://fonts.googleapis.com/css2?family=Righteous&display=swap");

body {
  font-family: "Righteous", cursive;
  color: #d14f4f;
  line-height: 1.7;
  font-size: 19px;
  background: #000000; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to bottom,
    #434343,
    #000000
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to bottom,
    #434343,
    #000000
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

/*navbar*/
.navbar {
  background-color: rgba(22, 22, 22, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
}

.link {
  color: #efefef;
  padding: 10px;
  font-size: 20px;
  text-decoration: none;
  transition: 0.5s ease all;
  text-align: center;
}

.link:hover {
  color: #f59494;
  transform: scale(2);
}

/* nasa photo */

.nasa-photo {
  display: flex;
  flex-direction: column;
  padding: 40px;
  margin: 50px auto;
  overflow-x: hidden;
}

.nasa-photo img {
  width: 35rem;
  height: auto;
  overflow: hidden;
  border: 1px solid #bbb;
  border-radius: 20px;
  margin: auto;
}

.nasa-photo h1 {
  margin: 20px auto;
}

.nasa-photo .date {
  margin-bottom: 30px;
}

.nasa-photo .explanation {
  color: #bbb;
  text-align: justify;
}

.nasa-video {
  margin: 50px auto;
}

@media (max-width: 1280px) {
  .nasa-photo img {
    width: 100%;
    margin-bottom: 30px;
  }
}

.home {
  background-image: url(./img/NasaPic.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: 100vh;
  width: 100vw;
}

.home-link {
  font-size: 35px;
  padding: 10px;
  color: #fff;
  text-decoration: none;
  transition: 0.5s ease all;
  text-transform: uppercase;
  display: flex;
  text-align: center;
  align-items: flex-start;
  justify-content: center;
  background-color: rgba(22, 22, 22, 0.2);
}

.home-link:hover {
  color: #434343;
}

@media all and (max-width: 500px) {
  .nasa-video {
    height: auto;
    width: auto;
  }
}
